<template>
	<view class="content">
		<!-- ËÆ¢Âçï‰ø°ÊÅØÂç°Áâá -->
		<view class="order-card">
			<view class="order-header">
				<text class="order-title">ËÆ¢ÂçïËØ¶ÊÉÖ</text>
				<text class="order-no">{{orderInfo.orderNo}}</text>
			</view>
			<view class="order-status-info">
				<text class="status" :class="orderInfo.status">{{orderInfo.statusText}}</text>
				<text class="order-time">‰∏ãÂçïÊó∂Èó¥Ôºö{{orderInfo.createTime || '2024-12-01 14:30'}}</text>
			</view>
		</view>

		<!-- Èó®Â∫ó‰ø°ÊÅØ -->
		<view class="store-card">
			<view class="card-title">Èó®Â∫ó‰ø°ÊÅØ</view>
			<view class="store-info">
				<view class="store-header">
					<text class="store-name">{{storeDetail.name}}</text>
					<text class="store-status" :class="storeDetail.status">{{storeDetail.statusText}}</text>
				</view>
				<view class="store-details">
					<view class="detail-item">
						<text class="detail-label">üìç Âú∞ÂùÄÔºö</text>
						<text class="detail-value">{{storeDetail.address}}</text>
					</view>
					<view class="detail-item">
						<text class="detail-label">üïê Ëê•‰∏öÊó∂Èó¥Ôºö</text>
						<text class="detail-value">{{storeDetail.businessHours}}</text>
					</view>
					<view class="detail-item">
						<text class="detail-label">üìû ËÅîÁ≥ªÁîµËØùÔºö</text>
						<text class="detail-value phone-number" @click="callStore">{{storeDetail.phone}}</text>
					</view>
				</view>
				<view class="store-actions">
					<button class="action-btn navigation" @click="navigateToStore">
						<text class="btn-icon">üó∫Ô∏è</text>
						<text>Èó®Â∫óÂØºËà™</text>
					</button>
					<button class="action-btn call" @click="showStoreGuide">
						<text class="btn-icon">üìç</text>
						<text>Âà∞Â∫óÊåáÂºï</text>
					</button>
				</view>
			</view>
		</view>

		<!-- È¢ÑËÆ¢‰ø°ÊÅØ -->
		<view class="booking-card">
			<view class="card-title">È¢ÑËÆ¢‰ø°ÊÅØ</view>
			<view class="booking-info">
				<view class="product-section">
					<image :src="orderInfo.image" class="product-image" mode="aspectFill"></image>
					<view class="product-details">
						<text class="product-name">{{orderInfo.productName}}</text>
						<text class="product-desc">{{orderInfo.description}}</text>
						<view class="booking-time">
							<text class="time-info">üìÖ {{orderInfo.date}} {{orderInfo.time}}</text>
							<text class="quantity-info">üë• {{orderInfo.quantity}}‰∫∫</text>
						</view>
					</view>
				</view>
				
				<!-- ÊàøÈó¥‰ø°ÊÅØ(Â¶ÇÊûúÂ∑≤ÂàÜÈÖç) -->
				<view class="room-info" v-if="orderInfo.roomNumber">
					<view class="room-header">
						<text class="room-title">ÂàÜÈÖçÊàøÈó¥</text>
						<text class="room-status" :class="getRoomStatusClass(orderInfo.roomStatus)">{{orderInfo.roomStatus}}</text>
					</view>
					<view class="room-details">
						<text class="room-number">ÊàøÈó¥Âè∑Ôºö{{orderInfo.roomNumber}}</text>
						<text class="unlock-count">Ââ©‰ΩôÂºÄÈîÅÊ¨°Êï∞Ôºö{{unlockRemaining}}/{{unlockLimit}}Ê¨°</text>
					</view>
				</view>

				<view class="price-summary">
					<text class="total-label">ÊÄªÈáëÈ¢ùÔºö</text>
					<text class="total-price">¬•{{orderInfo.totalPrice}}</text>
				</view>
			</view>
		</view>

		<!-- Êìç‰ΩúÊåâÈíÆÂå∫Âüü -->
		<view class="action-section">
			<button class="main-btn refund" @click="requestRefund" v-if="canRefund">
				<text class="btn-icon">üí∞</text>
				<text>Áî≥ËØ∑ÈÄÄÊ¨æ</text>
			</button>
			<button class="main-btn unlock" @click="unlockRoom" v-if="canUnlock" :disabled="!canUnlockNow">
				<text class="btn-icon">üîì</text>
				<text>{{unlockButtonText}}</text>
			</button>
			<view v-if="orderInfo.status === 'refunding'" class="status-info">
				<text class="status-text">ÈÄÄÊ¨æÁî≥ËØ∑Â∑≤Êèê‰∫§ÔºåËØ∑ËÄêÂøÉÁ≠âÂæÖÂ§ÑÁêÜ</text>
				<text class="status-note">È¢ÑËÆ°1-3‰∏™Â∑•‰ΩúÊó•Âà∞Ë¥¶</text>
			</view>
			<view v-if="orderInfo.status === 'refunded'" class="status-info refunded">
				<text class="status-text">ÈÄÄÊ¨æÂ∑≤ÂÆåÊàê</text>
				<text class="status-amount">ÈÄÄÊ¨æÈáëÈ¢ùÔºö¬•{{orderInfo.refundAmount}} ({{orderInfo.refundRate}}%)</text>
			</view>
		</view>

		<!-- ÈÄÄÊ¨æÁî≥ËØ∑ÂºπÁ™ó -->
		<uni-popup ref="refundPopup" type="center">
			<view class="refund-popup">
				<view class="popup-header">
					<text class="popup-title">Áî≥ËØ∑ÈÄÄÊ¨æ</text>
				</view>
				<view class="popup-content">
					<view class="refund-info">
						<text class="refund-text">ÈÄÄÊ¨æÈáëÈ¢ùÔºö¬•{{refundAmount}}</text>
						<text class="refund-rate">ÈÄÄÊ¨æÊØî‰æãÔºö{{refundRate}}%</text>
						<view class="refund-rules">
							<text class="rules-title">ÈÄÄÊ¨æËßÑÂàôÔºö</text>
							<text class="rule-item">‚Ä¢ È¢ÑÁ∫¶Êó∂Èó¥12Â∞èÊó∂Â§ñÔºåÂèØ100%ÈÄÄÊ¨æ</text>
							<text class="rule-item">‚Ä¢ È¢ÑÁ∫¶Êó∂Èó¥12Â∞èÊó∂Ëá≥6Â∞èÊó∂ÂÜÖÔºåÂèØ80%ÈÄÄÊ¨æ</text>
							<text class="rule-item">‚Ä¢ È¢ÑÁ∫¶Êó∂Èó¥6Â∞èÊó∂ÂÜÖÔºåÂèØ50%ÈÄÄÊ¨æ</text>
						</view>
						<text class="refund-note">ÈÄÄÊ¨æÂà∞Ë¥¶Êó∂Èó¥‰∏∫1-3‰∏™Â∑•‰ΩúÊó•</text>
					</view>
					<view class="reason-section">
						<text class="reason-label">ÈÄÄÊ¨æÂéüÂõ†Ôºö</text>
						<picker :value="refundReasonIndex" :range="refundReasons" @change="onRefundReasonChange">
							<view class="reason-picker">
								<text>{{refundReasons[refundReasonIndex]}}</text>
								<text class="picker-arrow">‚ñº</text>
							</view>
						</picker>
					</view>
					<view class="remark-section">
						<text class="remark-label">Â§áÊ≥®ËØ¥ÊòéÔºö</text>
						<textarea 
							v-model="refundRemark" 
							placeholder="ËØ∑ÊèèËø∞ÈÄÄÊ¨æÂéüÂõ†(ÂèØÈÄâ)" 
							class="remark-input"
							maxlength="200"
						></textarea>
					</view>
				</view>
				<view class="popup-actions">
					<button class="popup-btn cancel" @click="closeRefundPopup">ÂèñÊ∂à</button>
					<button class="popup-btn confirm" @click="confirmRefund">Á°ÆËÆ§ÈÄÄÊ¨æ</button>
				</view>
			</view>
		</uni-popup>

		<!-- Âà∞Â∫óÊåáÂºïËßÜÈ¢ëÂºπÁ™ó -->
		<uni-popup ref="storeGuidePopup" type="center">
			<view class="store-guide-popup">
				<view class="popup-header">
					<text class="popup-title">Âà∞Â∫óÊåáÂºï</text>
					<text class="popup-close" @click="closeStoreGuidePopup">‚úï</text>
				</view>
				<view class="popup-content">
					<view class="video-container">
						<video 
							:src="storeGuideVideo" 
							class="guide-video"
							controls
							autoplay
							show-center-play-btn
							show-play-btn
							show-fullscreen-btn
						></video>
					</view>
					<view class="guide-info">
						<text class="guide-title">{{storeDetail.name}} - Âà∞Â∫óÊåáÂºï</text>
						<text class="guide-desc">ËßÇÁúãËßÜÈ¢ë‰∫ÜËß£Â¶Ç‰ΩïÂà∞ËææÈó®Â∫óÔºåÂåÖÊã¨‰∫§ÈÄöÊñπÂºè„ÄÅÂú∞Ê†áÂª∫Á≠ëÁ≠â‰ø°ÊÅØ</text>
					</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				orderInfo: {},
				storeDetail: {},
				unlockRemaining: 3,
				unlockLimit: 3,
				refundReasonIndex: 0,
				refundReasons: [
					'‰∏¥Êó∂Êúâ‰∫ãÊó†Ê≥ïÂâçÂæÄ',
					'Èó®Â∫óÊúçÂä°ÈóÆÈ¢ò',
					'ËÆæÂ§áÊïÖÈöú',
					'Êó∂Èó¥ÂÜ≤Á™Å',
					'ÂÖ∂‰ªñÂéüÂõ†'
				],
				refundRemark: '',
				storeGuideVideo: '/static/video/store-guide.mp4' // Âà∞Â∫óÊåáÂºïËßÜÈ¢ëË∑ØÂæÑ
			}
		},
		computed: {
			canRefund() {
				// Â∑≤ÊîØ‰ªòÁöÑËÆ¢ÂçïÂèØ‰ª•Áî≥ËØ∑ÈÄÄÊ¨æ
				return this.orderInfo.status === 'paid';
			},
			canUnlock() {
				// Â∑≤ÊîØ‰ªò‰∏îÊúâÊàøÈó¥ÂàÜÈÖçÁöÑËÆ¢ÂçïÂèØ‰ª•ÂºÄÈîÅ
				return this.orderInfo.status === 'paid' && this.orderInfo.roomNumber;
			},
			canUnlockNow() {
				// ÊàøÈó¥ÂèØ‰ΩøÁî®Êàñ‰ΩøÁî®‰∏≠Ôºå‰∏îÊúâÂâ©‰ΩôÂºÄÈîÅÊ¨°Êï∞
				return (this.orderInfo.roomStatus === 'ÂèØ‰ΩøÁî®' || this.orderInfo.roomStatus === '‰ΩøÁî®‰∏≠') && this.unlockRemaining > 0;
			},
			unlockButtonText() {
				if (this.orderInfo.roomStatus !== 'ÂèØ‰ΩøÁî®' && this.orderInfo.roomStatus !== '‰ΩøÁî®‰∏≠') {
					return 'ÊàøÈó¥ÊöÇ‰∏çÂèØÁî®';
				}
				if (this.unlockRemaining <= 0) {
					return 'ÂºÄÈîÅÊ¨°Êï∞Â∑≤Áî®ÂÆå';
				}
				return `ÂºÄÈîÅ (${this.unlockRemaining}/${this.unlockLimit})`;
			},
			// ËÆ°ÁÆóÈÄÄÊ¨æÊØî‰æã
			refundRate() {
				const hoursUntilBooking = this.calculateHoursUntilBooking();
				if (hoursUntilBooking >= 12) {
					return 100;
				} else if (hoursUntilBooking >= 6) {
					return 80;
				} else {
					return 50;
				}
			},
			// ËÆ°ÁÆóÈÄÄÊ¨æÈáëÈ¢ù
			refundAmount() {
				const originalPrice = parseFloat(this.orderInfo.totalPrice);
				const refundAmount = (originalPrice * this.refundRate / 100).toFixed(2);
				return refundAmount;
			}
		},
		onLoad(options) {
			if (options.orderData) {
				this.orderInfo = JSON.parse(decodeURIComponent(options.orderData));
				this.loadStoreDetail();
				
				// ‰ªéÊú¨Âú∞Â≠òÂÇ®ÊÅ¢Â§çÂºÄÈîÅÊ¨°Êï∞
				const savedUnlockRemaining = uni.getStorageSync('unlockRemaining');
				if (savedUnlockRemaining !== null && savedUnlockRemaining !== undefined) {
					this.unlockRemaining = savedUnlockRemaining;
				}
			}
		},
		methods: {
			// ËÆ°ÁÆóË∑ùÁ¶ªÈ¢ÑÁ∫¶Êó∂Èó¥ËøòÊúâÂ§öÂ∞ëÂ∞èÊó∂
			calculateHoursUntilBooking() {
				try {
					// Ëé∑ÂèñÂΩìÂâçÊó∂Èó¥
					const now = new Date();
					
					// ÊûÑÂª∫È¢ÑÁ∫¶Êó∂Èó¥
					const bookingDateStr = `${this.orderInfo.date} ${this.orderInfo.time}`;
					const bookingTime = new Date(bookingDateStr);
					
					// ËÆ°ÁÆóÊó∂Èó¥Â∑ÆÔºàÊØ´ÁßíÔºâ
					const timeDiff = bookingTime.getTime() - now.getTime();
					
					// ËΩ¨Êç¢‰∏∫Â∞èÊó∂
					const hoursDiff = timeDiff / (1000 * 60 * 60);
					
					return Math.max(0, hoursDiff); // Á°Æ‰øù‰∏çËøîÂõûË¥üÊï∞
				} catch (error) {
					console.error('ËÆ°ÁÆóÈ¢ÑÁ∫¶Êó∂Èó¥Â§±Ë¥•:', error);
					return 0;
				}
			},
			
			loadStoreDetail() {
				// Ê†πÊçÆÈó®Â∫óÂêçÁß∞Âä†ËΩΩËØ¶ÁªÜ‰ø°ÊÅØ
				const storeMap = {
					'ÊúùÈò≥Â∫ó': {
						name: 'ÊúùÈò≥Â∫ó',
						address: 'Âåó‰∫¨Â∏ÇÊúùÈò≥Âå∫Âª∫ÂõΩË∑Ø88Âè∑Áé∞‰ª£ÂüéBÂ∫ß3Â±Ç',
						phone: '010-85888888',
						businessHours: '09:00-22:00',
						status: 'open',
						statusText: 'Ëê•‰∏ö‰∏≠',
						latitude: 39.9151,
						longitude: 116.4687
					},
					'Êµ∑Ê∑ÄÂ∫ó': {
						name: 'Êµ∑Ê∑ÄÂ∫ó', 
						address: 'Âåó‰∫¨Â∏ÇÊµ∑Ê∑ÄÂå∫‰∏≠ÂÖ≥ÊùëÂ§ßË°ó28Âè∑Êµ∑ÈæôÂ§ßÂé¶5Â±Ç',
						phone: '010-82666666',
						businessHours: '09:00-22:00',
						status: 'open',
						statusText: 'Ëê•‰∏ö‰∏≠',
						latitude: 39.9838,
						longitude: 116.3174
					},
					'Ë•øÂüéÂ∫ó': {
						name: 'Ë•øÂüéÂ∫ó',
						address: 'Âåó‰∫¨Â∏ÇË•øÂüéÂå∫Ë•øÂçïÂåóÂ§ßË°ó132Âè∑Â§™Âπ≥Ê¥ãÁôæË¥ß6Â±Ç',
						phone: '010-66777777',
						businessHours: '09:00-22:00',
						status: 'open', 
						statusText: 'Ëê•‰∏ö‰∏≠',
						latitude: 39.9084,
						longitude: 116.3736
					}
				};
				
				this.storeDetail = storeMap[this.orderInfo.store] || storeMap['ÊúùÈò≥Â∫ó'];
			},
			
			navigateToStore() {
				uni.openLocation({
					latitude: this.storeDetail.latitude,
					longitude: this.storeDetail.longitude,
					name: this.storeDetail.name,
					address: this.storeDetail.address,
					success: () => {
						console.log('ÂØºËà™ÊàêÂäü');
					},
					fail: (err) => {
						uni.showToast({
							title: 'ÂØºËà™Â§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},
			
			callStore() {
				uni.makePhoneCall({
					phoneNumber: this.storeDetail.phone,
					success: () => {
						console.log('Êã®ÊâìÁîµËØùÊàêÂäü');
					},
					fail: (err) => {
						uni.showToast({
							title: 'Êã®ÊâìÂ§±Ë¥•',
							icon: 'none'
						});
					}
				});
			},
			
			// ÊòæÁ§∫Âà∞Â∫óÊåáÂºïËßÜÈ¢ë
			showStoreGuide() {
				this.$refs.storeGuidePopup.open();
			},
			
			// ÂÖ≥Èó≠Âà∞Â∫óÊåáÂºïÂºπÁ™ó
			closeStoreGuidePopup() {
				this.$refs.storeGuidePopup.close();
			},
			
			requestRefund() {
				this.$refs.refundPopup.open();
			},
			
			closeRefundPopup() {
				this.$refs.refundPopup.close();
				this.refundRemark = '';
				this.refundReasonIndex = 0;
			},
			
			onRefundReasonChange(e) {
				this.refundReasonIndex = e.detail.value;
			},
			
			confirmRefund() {
				const hoursUntil = this.calculateHoursUntilBooking();
				const timeDesc = hoursUntil >= 12 ? '12Â∞èÊó∂Â§ñ' : hoursUntil >= 6 ? '12-6Â∞èÊó∂ÂÜÖ' : '6Â∞èÊó∂ÂÜÖ';
				
				uni.showModal({
					title: 'Á°ÆËÆ§ÈÄÄÊ¨æ',
					content: `Á°ÆÂÆöË¶ÅÁî≥ËØ∑ÈÄÄÊ¨æÔø•${this.refundAmount}ÂêóÔºü\n\nÈÄÄÊ¨æÂéüÂõ†Ôºö${this.refundReasons[this.refundReasonIndex]}\nÈÄÄÊ¨æÊØî‰æãÔºö${this.refundRate}% (${timeDesc})\nÈÄÄÊ¨æÂà∞Ë¥¶Êó∂Èó¥Ôºö1-3‰∏™Â∑•‰ΩúÊó•`,
					success: (res) => {
						if (res.confirm) {
							// Ê®°ÊãüÈÄÄÊ¨æÁî≥ËØ∑
							uni.showLoading({
								title: 'Êèê‰∫§‰∏≠...'
							});
							
							setTimeout(() => {
								uni.hideLoading();
								uni.showToast({
									title: 'ÈÄÄÊ¨æÁî≥ËØ∑Êèê‰∫§ÊàêÂäü',
									icon: 'success'
								});
								this.closeRefundPopup();
								
								// Êõ¥Êñ∞ËÆ¢ÂçïÁä∂ÊÄÅ
								this.orderInfo.status = 'refunding';
								this.orderInfo.statusText = 'ÈÄÄÊ¨æ‰∏≠';
							}, 2000);
						}
					}
				});
			},
			
			unlockRoom() {
				if (!this.canUnlockNow) {
					return;
				}
				
				// Ê†πÊçÆÊàøÈó¥Áä∂ÊÄÅÊòæÁ§∫‰∏çÂêåÁöÑÊèêÁ§∫‰ø°ÊÅØ
				const roomStatusText = this.orderInfo.roomStatus === '‰ΩøÁî®‰∏≠' ? 'ÔºàÊàøÈó¥Ê≠£Âú®‰ΩøÁî®‰∏≠Ôºâ' : '';
				
				uni.showModal({
					title: 'Á°ÆËÆ§ÂºÄÈîÅ',
					content: `Á°ÆÂÆöË¶ÅÂºÄÂêØÊàøÈó¥ ${this.orderInfo.roomNumber} ÂêóÔºü${roomStatusText}\n\nÂâ©‰ΩôÂºÄÈîÅÊ¨°Êï∞Ôºö${this.unlockRemaining}Ê¨°`,
					success: (res) => {
						if (res.confirm) {
							uni.showLoading({
								title: 'ÂºÄÈîÅ‰∏≠...'
							});
							
							// Ê®°ÊãüÂºÄÈîÅËøáÁ®ã
							setTimeout(() => {
								uni.hideLoading();
								
								// ÂáèÂ∞ëÂºÄÈîÅÊ¨°Êï∞
								this.unlockRemaining--;
								
								// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
								uni.setStorageSync('unlockRemaining', this.unlockRemaining);
								
								// ÊòæÁ§∫ÂºÄÈîÅÊàêÂäüÊèêÁ§∫
								uni.showToast({
									title: 'ÂºÄÈîÅÊàêÂäüÔºÅ',
									icon: 'success'
								});
								
								// Â¶ÇÊûúËøòÊúâÂâ©‰ΩôÊ¨°Êï∞ÔºåÊòæÁ§∫ÁªßÁª≠ÂºÄÈîÅÁöÑÊèêÁ§∫
								if (this.unlockRemaining > 0) {
									setTimeout(() => {
										const statusText = this.orderInfo.roomStatus === '‰ΩøÁî®‰∏≠' ? 'ÔºàÊàøÈó¥Ê≠£Âú®‰ΩøÁî®‰∏≠Ôºâ' : '';
										uni.showModal({
											title: 'ÂºÄÈîÅÊàêÂäü',
											content: `ÊàøÈó¥ ${this.orderInfo.roomNumber} Â∑≤ÂºÄÂêØÔºÅ${statusText}\n\nÂâ©‰ΩôÂºÄÈîÅÊ¨°Êï∞Ôºö${this.unlockRemaining}Ê¨°\n\nÊÇ®ÂèØ‰ª•ÁªßÁª≠‰ΩøÁî®ÂºÄÈîÅÂäüËÉΩÔºåÁõ¥Âà∞Ê¨°Êï∞Áî®ÂÆå„ÄÇ`,
											confirmText: 'Áü•ÈÅì‰∫Ü',
											showCancel: false
										});
									}, 1000);
								} else {
									// Ê¨°Êï∞Áî®ÂÆåÊó∂ÁöÑÊèêÁ§∫
									setTimeout(() => {
										const statusText = this.orderInfo.roomStatus === '‰ΩøÁî®‰∏≠' ? 'ÔºàÊàøÈó¥Ê≠£Âú®‰ΩøÁî®‰∏≠Ôºâ' : '';
										uni.showModal({
											title: 'ÂºÄÈîÅÊ¨°Êï∞Â∑≤Áî®ÂÆå',
											content: `ÊàøÈó¥ ${this.orderInfo.roomNumber} Â∑≤ÂºÄÂêØÔºÅ${statusText}\n\nÊÇ®ÁöÑÂºÄÈîÅÊ¨°Êï∞Â∑≤Áî®ÂÆåÔºåÂ¶ÇÈúÄÁªßÁª≠‰ΩøÁî®ÔºåËØ∑ËÅîÁ≥ªÂÆ¢Êúç„ÄÇ`,
											confirmText: 'Áü•ÈÅì‰∫Ü',
											showCancel: false
										});
									}, 1000);
								}
							}, 2000);
						}
					}
				});
			},
			
			getRoomStatusClass(status) {
				switch(status) {
					case 'ÂèØ‰ΩøÁî®':
						return 'status-available';
					case 'Ê∏ÖÊ¥Å‰∏≠':
						return 'status-cleaning';
					case '‰ΩøÁî®‰∏≠':
						return 'status-using';
					default:
						return '';
				}
			}
		}
	}
</script>

